import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withStyles, Grid, Typography, Button, Breadcrumbs, TextField, Dialog, CircularProgress } from '@material-ui/core';
import { FiChevronRight } from "react-icons/fi"
import Autocomplete from '@material-ui/lab/Autocomplete';
import { addProducts } from "../../../store/actions/product_action";

/**Created common styles for UI.*/
const styles = theme => ({
	root: {
		padding: theme.spacing(4)
	},
	paper: {
		backgroundColor: "#fff",
		marginTop: 15,
		padding: theme.spacing(2)
	},
	title: {
		fontSize: 16,
		color: "#000",
		fontWeight: "bold",
		margin: "10px 0px 20px",
		fontFamily: "SourceSansPro-Regular"
	},
	textFiledText: {
		fontSize: 13,
		padding: "5px 0px",
		color: "#000",
		fontFamily: "SourceSansPro-Regular"
	},
	inputRoot: {
		backgroundColor: "#FFFFFF",
		border: "1px solid #E1E7ED",
		borderRadius: "4px",
		fontSize: "13x !important",
		fontFamily: "SourceSansPro-Regular",
		"&:hover": {
			border: "1px solid #1B1B1B",
			backgroundColor: "#FFFFFF"
		}
	},
	inputStyle: {
		fontFamily: "SourceSansPro-Regular !important",
		fontSize: "13x !important",
		padding: "10px !important",
		color: "#1B1B1B",
		opacity: 1,
		"&&:after": {
			color: "#1B1B1B",
		}
	},
	underline: {
		"&&&:before": {
			borderBottom: "none"
		},
		"&&:after": {
			borderBottom: "none"
		}
	},
	dragContainer: {
		width: "97%",
		height: 100,
		padding: 15,
		border: "1px solid #cccccc",
		backgroundColor: "#FAFAFA",
		textAlign: "center",
		fontFamily: "SourceSansPro-Regular"
	},
	buttonVariant: {
		boxShadow: "0px 0px 0px 0px #E1E7ED",
		backgroundColor: "#1B1B1B",
		margin: "5px 0px",
		color: "#fff",
		borderRadius: 6,
		fontSize: 14,
		padding: "5px 20px",
		height: "fit-content",
		textTransform: "none",
		fontFamily: "SourceSansPro-Regular",
		'&:hover': {
			backgroundColor: "#1B1B1B",
		},
	},
	uploadButton: {
		boxShadow: "0px 0px 0px 0px #E1E7ED",
		backgroundColor: "#cccccc",
		margin: "10px 0px 25px",
		color: "#000",
		borderRadius: 0,
		fontSize: 14,
		padding: "3px 20px",
		height: "fit-content",
		textTransform: "none",
		fontFamily: "SourceSansPro-Regular",
		'&:hover': {
			backgroundColor: "#cccccc",
		},
	},
	inputimage: {
		display: "none"
	},
	dialogPaper: {
		backgroundColor: "rgba(0,0,0,0)",
		boxShadow: "none",
		overflow: "hidden"
	},
});

/**
 * @class
 * Class representing AddProduct component
 */
class AddProduct extends Component {
	constructor(props) {
		super(props);

		this.state = {
			productName: "",
			quantity: "",
			price: "",
			productCode: "",
			manufacturer: "",
			productSKU: "",
			productTax: "",
			coolingCapacity: "",
			powerConsumption: "",
			currentRating: "",
			fcuCapacity: "",
			efficiencyProfile: "",
			productCategoryId: "",
			Costprice: "",

			productName_error: false,
			quantity_error: false,
			price_error: false,
			productCode_error: false,
			manufacturer_error: false,
			productSKU_error: false,
			productTax_error: false,
			coolingCapacity_error: false,
			powerConsumption_error: false,
			currentRating_error: false,
			fcuCapacity_error: false,
			efficiencyProfile_error: false,
			productCategoryId_error: false,
			Costprice_error: false,

			file: null,
			imgSrc: null,
			loading: false
		};
	}

	dragOver = (e) => {
		e.preventDefault();
	}

	dragEnter = (e) => {
		e.preventDefault();
	}

	dragLeave = (e) => {
		e.preventDefault();
	}

	fileDrop = (e) => {
		e.preventDefault();
		const files = e.dataTransfer.files;
		let file = files[0];
		if (typeof file === 'object') {
			let reader = new FileReader();
			reader.readAsDataURL(file);
			reader.onloadend = function (e) {
				this.setState({
					imgSrc: [reader.result]
				})
			}.bind(this);
			this.setState({ file: files[0] });
		}
	}

	fileHandler = (event) => {
		let file = event.target.files[0];
		if (typeof file === 'object') {
			let reader = new FileReader();
			reader.readAsDataURL(file);
			reader.onloadend = function (e) {
				this.setState({
					imgSrc: [reader.result]
				})
			}.bind(this);
			this.setState({ file: event.target.files[0] });
		}
	}

	submit = () => {
		let isError = false;
		let { productName,
			quantity,
			price,
			productCode,
			manufacturer,
			productSKU,
			productTax,
			coolingCapacity,
			powerConsumption,
			currentRating,
			efficiencyProfile,
			productCategoryId,
			Costprice,
		} = this.state;


		if (isError === false) {
			let data = {}
			data.productName = productName;
			data.quantity = quantity;
			data.price = price;
			data.productCode = productCode;
			data.manufacturer = manufacturer;
			data.productSKU = productSKU;
			data.productTax = productTax;
			data.coolingCapacity = coolingCapacity;
			data.powerConsumption = powerConsumption;
			data.currentRating = currentRating;
			data.fcuCapacity = currentRating;
			data.efficiencyProfile = efficiencyProfile;
			data.productCategoryId = productCategoryId;
			data.Costprice = Costprice;
			this.setState({ loading: true })
			this.props.dispatch(addProducts(this, data))
		}

	}

	/** rendering Add Product information. */
	render() {
		const { classes } = this.props;

		const tags = [
			{ title: 'tag1', year: 1921 },
			{ title: 'tag2', year: 2009 },
			{ title: 'tag3', year: 2000 },
			{ title: 'tag4', year: 2009 },
			{ title: 'tag5', year: 1975 },
		];
		return (
			<div className={classes.root}>
				<Breadcrumbs separator={<FiChevronRight size={15} />} aria-label="breadcrumb">
					<Typography onClick={() => this.props.history.push("/admin/products")} style={{ fontFamily: "SourceSansPro-Regular", color: "#1B1B1B", cursor: "pointer", fontSize: 15 }} color="primary" >
						Products
					</Typography>
					<Typography style={{ fontFamily: "SourceSansPro-Regular" }} color="textPrimary">Add New Product</Typography>
				</Breadcrumbs>
				<div className={classes.paper}>
					<Typography className={classes.title}>Basic Information</Typography>
					<Grid container spacing={2}>
						<Grid item xs={12} sm={6}>
							<Typography className={classes.textFiledText}>Product Name</Typography>
							<TextField
								placeholder="Enter Product Name"
								variant="filled"
								onChange={(event) => this.setState({ productName: event.target.value, productName_error: false, error_text: null })}
								InputProps={{ classes: { input: classes.inputStyle, underline: classes.underline, root: classes.inputRoot } }}
								fullWidth
								autoComplete="off"
							/>
						</Grid>
						<Grid item xs={12} sm={6}>
							<Typography className={classes.textFiledText}>Category</Typography>
							<TextField
								variant="filled"
								value={this.state.productCategoryId}
								onChange={(event) => this.setState({ productCategoryId: event.target.value, productCategoryId_error: false })}
								InputProps={{ classes: { input: classes.inputStyle, underline: classes.underline, root: classes.inputRoot } }}
								fullWidth
								select
								SelectProps={{
									native: true,
								}}
								autoComplete="off"
								error={this.state.productCategoryId_error ? true : false}
								helperText={this.state.productCategoryId_error ? "Please enter product category" : false}
							>
								<option value="" disabled style={{ fontFamily: "SourceSansPro-Regular" }}>
									Select
								</option>
								<option value="1" style={{ fontFamily: "SourceSansPro-Regular" }}>
									Electric
								</option>
								<option value="2" style={{ fontFamily: "SourceSansPro-Regular" }}>
									Energy
								</option>
								<option value="3" style={{ fontFamily: "SourceSansPro-Regular" }}>
									Condenser
								</option>
								<option value="4" style={{ fontFamily: "SourceSansPro-Regular" }}>
									FCU
								</option>
							</TextField>
						</Grid>
						<Grid item xs={12} sm={6}>
							<Typography className={classes.textFiledText}>Product Code</Typography>
							<TextField
								placeholder="Enter Product Code"
								variant="filled"
								value={this.state.productCode}
								onChange={(event) => this.setState({ productCode: event.target.value, productCode_error: false })}
								InputProps={{ classes: { input: classes.inputStyle, underline: classes.underline, root: classes.inputRoot } }}
								fullWidth
								autoComplete="off"
							/>
						</Grid>
						<Grid item xs={12} sm={6}>
							<Typography className={classes.textFiledText}>Product SKU</Typography>
							<TextField
								placeholder="Enter Product SKU"
								variant="filled"
								value={this.state.productSKU}
								onChange={(event) => this.setState({ productSKU: event.target.value, productSKU_error: false })}
								InputProps={{ classes: { input: classes.inputStyle, underline: classes.underline, root: classes.inputRoot } }}
								fullWidth
								autoComplete="off"
							/>
						</Grid>
						<Grid item xs={12} sm={6}>
							<Typography className={classes.textFiledText}>Manufacturer</Typography>
							<TextField
								placeholder="Enter Brand Name"
								variant="filled"
								value={this.state.manufacturer}
								onChange={(event) => this.setState({ manufacturer: event.target.value, manufacturer_error: false })}
								InputProps={{ classes: { input: classes.inputStyle, underline: classes.underline, root: classes.inputRoot } }}
								fullWidth
								autoComplete="off"
							/>
						</Grid>
						<Grid item xs={12} sm={6}>
							<Typography className={classes.textFiledText}>Quantity</Typography>
							<TextField
								placeholder="Enter Qunatity"
								variant="filled"
								value={this.state.quantity}
								onChange={(event) => this.setState({ quantity: event.target.value, quantity_error: false })}
								InputProps={{ classes: { input: classes.inputStyle, underline: classes.underline, root: classes.inputRoot } }}
								fullWidth
								autoComplete="off"
							/>
						</Grid>
						<Grid item xs={12} sm={6}>
							<Typography className={classes.textFiledText}>Price</Typography>
							<TextField
								placeholder="Enter Product price"
								variant="filled"
								value={this.state.price}
								onChange={(event) => this.setState({ price: event.target.value, price_error: false })}
								InputProps={{ classes: { input: classes.inputStyle, underline: classes.underline, root: classes.inputRoot } }}
								fullWidth
								autoComplete="off"
							/>
						</Grid>
						<Grid item xs={12} sm={6}>
							<Typography className={classes.textFiledText}>Tax(%)</Typography>
							<TextField
								placeholder="Enter tax"
								variant="filled"
								value={this.state.productTax}
								onChange={(event) => this.setState({ productTax: event.target.value, productTax_error: false })}
								InputProps={{ classes: { input: classes.inputStyle, underline: classes.underline, root: classes.inputRoot } }}
								fullWidth
								autoComplete="off"
							/>
						</Grid>

						<Grid item xs={12} sm={6}>
							<Typography className={classes.textFiledText}>Cooling Capacity</Typography>
							<TextField
								placeholder="Enter Cooling Capacity"
								variant="filled"
								value={this.state.coolingCapacity}
								onChange={(event) => this.setState({ coolingCapacity: event.target.value, coolingCapacity_error: false })}
								InputProps={{ classes: { input: classes.inputStyle, underline: classes.underline, root: classes.inputRoot } }}
								fullWidth
								autoComplete="off"
							/>
						</Grid>

						<Grid item xs={12} sm={6}>
							<Typography className={classes.textFiledText}>Power Consumption</Typography>
							<TextField
								placeholder="Enter Power Consumption"
								variant="filled"
								value={this.state.powerConsumption}
								onChange={(event) => this.setState({ powerConsumption: event.target.value, powerConsumption_error: false })}
								InputProps={{ classes: { input: classes.inputStyle, underline: classes.underline, root: classes.inputRoot } }}
								fullWidth
								autoComplete="off"
							/>
						</Grid>

						<Grid item xs={12} sm={6}>
							<Typography className={classes.textFiledText}>Current Rating</Typography>
							<TextField
								placeholder="Enter Current Rating"
								variant="filled"
								value={this.state.currentRating}
								onChange={(event) => this.setState({ currentRating: event.target.value, currentRating_error: false })}
								InputProps={{ classes: { input: classes.inputStyle, underline: classes.underline, root: classes.inputRoot } }}
								fullWidth
								autoComplete="off"
							/>
						</Grid>

						<Grid item xs={12} sm={6}>
							<Typography className={classes.textFiledText}>FCU Capacities</Typography>
							<TextField
								placeholder="Enter FCU Capacities"
								variant="filled"
								value={this.state.fcuCapacity}
								onChange={(event) => this.setState({ fcuCapacity: event.target.value, fcuCapacity_error: false })}
								InputProps={{ classes: { input: classes.inputStyle, underline: classes.underline, root: classes.inputRoot } }}
								fullWidth
								autoComplete="off"
							/>
						</Grid>

						<Grid item xs={12} sm={6}>
							<Typography className={classes.textFiledText}>Effiency Profile</Typography>
							<TextField
								placeholder="Enter Effiency Profile"
								variant="filled"
								value={this.state.efficiencyProfile}
								onChange={(event) => this.setState({ efficiencyProfile: event.target.value, efficiencyProfile_error: false })}
								InputProps={{ classes: { input: classes.inputStyle, underline: classes.underline, root: classes.inputRoot } }}
								fullWidth
								autoComplete="off"
							/>
						</Grid>

						<Grid item xs={12} sm={6}>
							<Typography className={classes.textFiledText}>Cost Price</Typography>
							<TextField
								placeholder="Enter Cost price"
								variant="filled"
								value={this.state.Costprice}
								onChange={(event) => this.setState({ Costprice: event.target.value, Costprice_error: false })}
								InputProps={{ classes: { input: classes.inputStyle, underline: classes.underline, root: classes.inputRoot } }}
								fullWidth
								autoComplete="off"
							/>
						</Grid>

						<Grid item xs={12}>
							<Typography className={classes.textFiledText}>Tags</Typography>
							<Autocomplete
								multiple
								id="tags-outlined"
								className={classes.autoComplete}
								options={tags}
								getOptionLabel={(option) => option.title}
								filterSelectedOptions
								renderInput={(params) => (
									<TextField
										{...params}
										variant="outlined"
										placeholder="Select"
									/>
								)}
							/>
						</Grid>
						<Grid item xs={12}>
							{this.state.file !== null ?
								<div className={classes.dragContainer} style={{ display: "block", height: 150 }}>
									<div>
										<img src={this.state.imgSrc} alt="org" width={100} height="auto" />
									</div>
									<div>
										<input
											accept="image/*"
											className={classes.inputimage}
											id="text-button-file1"
											multiple
											type="file"
											onChange={this.fileHandler}
										/>
										<label htmlFor="text-button-file1">
											<Button component="span" variant="contained" className={classes.uploadButton} >
												Change
											</Button>
										</label>
									</div>
								</div>
								:
								<div className={classes.dragContainer}
									onDragOver={this.dragOver}
									onDragEnter={this.dragEnter}
									onDragLeave={this.dragLeave}
									onDrop={this.fileDrop}
								>
									<Typography style={{ fontSize: 14, color: "#000", padding: "10px 0px", fontFamily: "SourceSansPro-Regular" }}>Drag and drop files here</Typography>
									<Typography style={{ fontSize: 13, color: "#666666", fontFamily: "SourceSansPro-Regular" }}>or</Typography>
									<input
										accept="image/*"
										className={classes.inputimage}
										id="text-button-file1"
										multiple
										type="file"
										onChange={this.fileHandler}
									/>
									<label htmlFor="text-button-file1">
										<Button component="span" variant="contained" className={classes.uploadButton} >
											Upload
										</Button>
									</label>

								</div>}
						</Grid>
					</Grid>
					<div style={{ width: "100%", display: "flex", justifyContent: "flex-end", marginTop: 15 }} >
						<Button variant="contained" className={classes.buttonVariant} onClick={() => this.submit()}>
							Create Product
						</Button>
					</div>
				</div>
				<Dialog open={this.state.loading}
					classes={{
						paper: classes.dialogPaper,
					}}
				>
					<div style={{ boxShadow: 0, overflow: "none" }}>
						<CircularProgress color="primary" size={50} />
					</div>
				</Dialog>
			</div>
		);
	}
}

/** Get data from store and assign to props. */
const mapStateToProps = () => ({

});

export default connect(
	mapStateToProps
)(withStyles(styles)(AddProduct));
